<template>
  <div class = "content">
    <h1 class = "title">
      <slot name = "title"></slot>
    </h1>
    <ul>
      <li>
        <div class = "song-item">
          <span class = "item-index"></span>
          <span class = "item-title"></span>
          <span class = "item-name"></span>
          <span class = "item-intro"></span>
        </div>
      </li>
      <li v-for = "(item,index) in songList" :key = "index">
        {{ isCollected(item) }}
        <div class = "song-item">
          <span class = "item-index">
            <span @click = "toplay(item.id,item.url,item.pic,index,item.name,item.lyric)">
              {{ index + 1 }}
            </span>
          </span>
          <span class = "item-title">
              <span @click = "toplay(item.id,item.url,item.pic,index,item.name,item.lyric)">
                {{ replaceFName(item.name) }}
              </span>
          </span>
          <span class = "item-name">
            <span @click = "goSinger(replaceLName(item.name))">
              {{ replaceLName(item.name) }}
            </span>
          </span>
          <span class = "item-intro">
            <span>
              {{ item.introduction }}
            </span>
          </span>
          <span class = "icons" @click = "showSelect($event,index,item.id)" ref = "add">
            <add-icon></add-icon>
          </span>
          <span class = "icons" v-if = "isMySongList" @click = "removeSong(item.id)">
            <delete-icon></delete-icon>
          </span>
          <span class = "icons" @click = "handleCollect(item)">
            <svg :class = "{active: item.collection}" class = "icon">
              <use xlink:href = "#icon-xihuan-shi"></use>
            </svg>
          </span>
          <span class="icons" @click="showComplaintModal">
          <!--  -->
            <img src='../assets/js/icon/icon-complain.svg' class="icon">
          </span>
          <div class="complaint-modal" v-if="showModal">
            <div class="modal-overlay"></div>
            <div class="modal-content">
              <h3></h3>
              <div class="reason-group" v-for="(group, index) in reasonGroups" :key="index">
                <h4>{{ group.title }}</h4>
                <div class="reason-list">
                  <div v-for="(reason, reasonIndex) in group.reasons" :key="reasonIndex">
                    <input
                      type="checkbox"
                      :id="`reason${index}${reasonIndex}`"
                      :value="reason.value"
                      v-model="selectedReasons"
                      @change="checkMaxReasons"
                    >
                    <label :for="`reason${index}${reasonIndex}`">{{ reason.label }}</label>
                  </div>
                </div>
              </div>
              <!-- <div class="reason-group">
                <h4></h4>
                <div class="reason-list">
                  <input type="checkbox" id="legalViolation1" value="" v-model="selectedReasons">
                  <label for="legalViolation1"></label>
                  <input type="checkbox" id="legalViolation2" value="" v-model="selectedReasons">
                  <label for="legalViolation2"></label>
                  <input type="checkbox" id="legalViolation3" value="" v-model="selectedReasons">
                  <label for="legalViolation3"></label>
                  <input type="checkbox" id="legalViolation4" value="" v-model="selectedReasons">
                  <label for="legalViolation4"></label>
                </div>
              </div>
              <div class="reason-group">
                <h4></h4>
                <div class="reason-list">
                  <input type="checkbox" id="rumor1" value="" v-model="selectedReasons">
                  <label for="rumor1"></label>
                  <input type="checkbox" id="rumor2" value="" v-model="selectedReasons">
                  <label for="rumor2"></label>
                  <input type="checkbox" id="rumor3" value="" v-model="selectedReasons">
                  <label for="rumor3"></label>
                </div>
              </div>
              <div class="reason-group">
                <h4></h4>
                <div class="reason-list">
                  <input type="checkbox" id="irregularPost1" value="" v-model="selectedReasons">
                  <label for="irregularPost1"></label>
                  <input type="checkbox" id="irregularPost2" value="" v-model="selectedReasons">
                  <label for="irregularPost2"></label>
                  <input type="checkbox" id="irregularPost3" value="" v-model="selectedReasons">
                  <label for="irregularPost3"></label>
                  <input type="checkbox" id="irregularPost4" value="" v-model="selectedReasons">
                  <label for="irregularPost4"></label>
                </div>
              </div>
              <div class="reason-group">
                <h4></h4>
                <div class="reason-list">
                  <input type="checkbox" id="unfriendly1" value="" v-model="selectedReasons">
                  <label for="unfriendly1"></label>
                  <input type="checkbox" id="unfriendly2" value="" v-model="selectedReasons">
                  <label for="unfriendly2"></label>
                </div>
              </div>
              <div class="reason-group">
                <h4></h4>
                <div class="reason-list">
                  <input type="checkbox" id="publicOrder1" value="" v-model="selectedReasons">
                  <label for="publicOrder1"></label>
                  <input type="checkbox" id="publicOrder2" value="" v-model="selectedReasons">
                  <label for="publicOrder2"></label>
                  <input type="checkbox" id="publicOrder3" value="" v-model="selectedReasons">
                  <label for="publicOrder3"></label>
                  <input type="checkbox" id="publicOrder4" value="" v-model="selectedReasons">
                  <label for="publicOrder4"></label>
                  <input type="checkbox" id="publicOrder5" value="" v-model="selectedReasons">
                  <label for="publicOrder4"></label>
                  <input type="checkbox" id="publicOrder6" value="" v-model="selectedReasons">
                  <label for="publicOrder5"></label>
                </div>
              </div> -->
              <div class="reason-group">
                <h4></h4>
                <textarea class="description-textarea" v-model="description" placeholder=""></textarea>
              </div>
              <div class="modal-footer">
                <button class = "btn btn-blue" @click="submitComplaint(item.id)"></button>
                <button class = "btn btn-gray" @click="showModal = false"></button>
              </div>
            </div>
          </div>
        </div>
      </li>
    </ul>
    <div class = "select-song-list" v-bind:style = "selectPosition" ref = "select" v-if = "isShowSelect">
      <div class = "select-item"
           v-for = "(item, index) in this.selectData" :key = "index"
           @click = "addToSongList(item.id)">
        <span class = "img-container">
          <img class = "img" :src = "attachImageUrl(item.pic)" alt = "">
        </span>
        <div class = "select-title">{{ item.title }}</div>
      </div>
    </div>
    <el-dialog
      :visible.sync = "this.deleteDialog"
      center
      top = "20%"
      width = "400px"
    >
      <span style = "margin-left: 30%;font-size: 20px">?</span>
      <span slot = "footer">
      <el-button @click = "cancelDelete()" size = "small"></el-button>
      <el-button @click = "confirmDelete()" size = "small" type = "primary"></el-button>
    </span>
    </el-dialog>
  </div>
</template>

<script>
import {mixin} from '../mixins'
import {
  deleteCollectSong,
  getOneSingerByName,
  setCollect,
  existCollectSong,
  selectSongListByUserId,
  addListSong, selectByPrimaryKey, deleteListSong, addComplaint
} from "../api";
import {mapGetters} from "vuex";
import AddIcon from "../assets/icon/addIcon.vue";
import DeleteIcon from "../assets/icon/deleteIcon.vue";
import song from "../store/song";

export default {
  name: 'album-content',
  components: {DeleteIcon, AddIcon},
  mixins: [mixin],
  props: ['songList'],
  computed: {
    ...mapGetters([
      'loginIn',
      'userId',
    ]),
  },
  data() {
    return {
      deleteDialog: false,
      isMySongList: false,
      isShowSelect: false,
      isSidebarVisible: false,
      deleteSongId: '',
      addIndex: '',
      selectSongId: '',
      selectData: [],
      selectPosition: {
        position: 'fixed',
        top: 0,
        left: 0,
      },
      showModal: false,
      selectedReasons: [],
      description: '',
      reasonGroups: [
        {
        title: '',
        reasons: [
            { value: '', label: '' },
            { value: '', label: '' },
            { value: '', label: '' },
            { value: '', label: '' },
            // ... 
          ],
        },
        {
          title: '',
          reasons: [
            { value: '', label: '' },
            { value: '', label: '' },
            { value: '', label: '' },
            // ... 
          ],
        },
        {
          title: '',
          reasons: [
            { value: '', label: '' },
            { value: '', label: '' },
            { value: '', label: '' },
            { value: '', label: '' },
            // ... 
          ],
        },
        {
          title: '',
          reasons: [
            { value: '', label: '' },
            { value: '', label: '' },
            // ... 
          ],
        },
        {
          title: '',
          reasons: [
            { value: '', label: '' },
            { value: '', label: '' },
            { value: '', label: '' },
            { value: '', label: '' },
            { value: '', label: '' },
            { value: '', label: '' },
            // ... 
          ],
        },
      ],
    }
  },
  mounted() {
    if (this.loginIn)
    {
      selectSongListByUserId(this.userId).then(res =>
      {
        res.forEach(item =>
        {
          this.selectData.push({id: item.id, pic: item.pic, title: item.title})
        })
      })
      selectByPrimaryKey(this.$route.params.id).then(res =>
      {
        if (res.userId === this.userId && this.$route.path.includes('song-list-album'))
        {
          this.isMySongList = true;
        }
      })
    }
  },
  methods: {
    isCollected(item) {
      if (!this.loginIn)
      {
        this.$set(item, 'collection', false);
      }
      else {
        existCollectSong(this.userId, item.id).then(res =>
        {
          this.$set(item, 'collection', res);
        })
      }
    },
    handleCollect(item) {
      let songId = item.id;
      if (this.loginIn) {
        let params = new URLSearchParams();
        params.append('userId', this.userId);
        params.append('type', 0);
        params.append('songId', songId);
        setCollect(params)
          .then(res =>
          {
            if (res.code === 1) {
              item.collection = true;
              this.notify('', 'success');
            }
            else if (res.code === 2) {
              deleteCollectSong(this.userId, songId);
              item.collection = false;
              this.notify('', 'success');
            }
            else {
              this.notify('', 'error');
            }
          })
      }
      else {
        this.notify('', 'warning');
      }
    },
    goSinger(name) {
      getOneSingerByName(name).then(res =>
      {
        this.$store.commit('setTempList', res);
        this.$router.push({path: `/singer-album/${res.id}`});
      })
    },
    addToSongList(songListId) {
      let params = new URLSearchParams();
      params.append('songId', this.selectSongId);
      params.append('songListId', songListId);
      addListSong(params).then(res =>
      {
        if (res.code)
        {
          this.hideSelectBox();
          this.notify('', 'success');
        }
      })
    },
    showSelect(event, index, songId) {
      if (!this.loginIn)
      {
        this.notify('', 'warning');
      }
      else {
        this.isShowSelect = true;
        this.addIndex = index;
        this.selectSongId = songId;
        const svgRect = event.target.getBoundingClientRect();
        this.selectPosition.top = svgRect.top + 20 + 'px';
        this.selectPosition.left = svgRect.right + 20 + 'px';
        window.addEventListener('click', this.handleOutsideClick);
      }
    },
    handleOutsideClick(event) {
      if (!this.$refs.add[this.addIndex].contains(event.target) && !this.$refs.select.contains(event.target)) {
        this.hideSelectBox();
      }
    },
    hideSelectBox() {
      this.isShowSelect = false;
      this.addIndex = '';
      this.selectSongId = '';
      window.removeEventListener('click', this.handleOutsideClick);
    },
    removeSong(songId) {
      this.deleteSongId = songId;
      this.deleteDialog = true;
    },
    cancelDelete() {
      this.deleteDialog = false;
      this.deleteSongId = '';
    },
    confirmDelete() {
      deleteListSong(this.deleteSongId, this.$route.params.id).then(res =>
      {
        if (res)
        {
          let list = this.songList.filter(song => song.id !== this.deleteSongId);
          this.$store.commit('setListOfSongs',list);
          this.deleteDialog = false;
          this.deleteSongId = '';
          this.notify('', 'success');
        }
      })
    },
    showComplaintModal() {
      this.showModal = true; // 
      this.selectedReasons = []; // 
      this.description = ''; // 
    },
    submitComplaint(Id) {
      if (this.selectedReasons.length === 0) {
        alert('');
        return;
      }
      // 
      let params = new URLSearchParams();
      params.append("userId", this.userId);
      params.append("songId", Id); // item
      params.append("content", this.description);
      // 
      params.append("reasons", this.selectedReasons.join(',')+';'+this.description);
      console.log(':' + this.selectedReasons.join(',')+'\n:'+this.description);
      this.showModal = false;
      // 
      // addComplaint(params)
      //   .then(res => {
      //     this.notify('', 'success');
      //     this.showModal = false; // 
      //   })
      //   .catch(error => {
      //     console.log(error);
      //     this.notify('', 'error'); // 
      //     this.showModal = false; // 
      //   });
    },
    notify(message, type) {
      // 
      // alerttoast
      alert(message);
    },

    checkMaxReasons() {
      if (this.selectedReasons.length > 3) {
        // checkbox
        alert('');
        // disabledcheckbox
        // 
        // 
        this.selectedReasons.splice(3);
      }
    },
  },
}
</script>

<style scoped>
/*  */
.complaint-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /*  */
}

.modal-overlay {
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.modal-content {
  background: #fff;
  padding: 30px; /*  */
  border-radius: 8px; /*  */
  width: 80%; /* 80% */
  max-width: 80vh; /*  */
  max-height: 60vh; /* 80% */
  position: relative; /*  */
  z-index: 1001; /* overlay */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /*  */
  overflow: auto; /*  */
  display: flex;
  flex-direction: column;
  height: 100%; /*  */
}

.modal-body {
  /* body.modal-contentfooter */
  flex: 1; /*  */
}

.reason-group {
  margin-bottom: 20px; /*  */
}

.reason-list input[type="checkbox"] {
  margin-right: 10px; /*  */
}

.description-textarea {
  height: 200px;
  width: 800px;
  max-width: 100%;
  padding: 10px; /*  */
  border: 1px solid #ccc; /*  */
  border-radius: 5px; /*  */
  font-size: 16px; /*  */
  font-family: inherit; /*  */
  resize: none; /*  */
  transition: border-color 0.3s ease; /*  */
}

.description-textarea:focus {
  border-color: #007bff; /*  */
  outline: none; /*  */
  box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25); /*  */
}

.modal-footer {
  display: flex;
  justify-content: center; /* space-between */
  align-items: center; /*  */
  padding: 10px; /*  */
  background-color: #f5f5f5; /* footer */
  margin-top: auto;
}

.btn {
  padding: 10px 20px; /*  */
  margin-left: 10px; /*  */
  font-size: 16px; /*  */
  border: none; /*  */
  border-radius: 4px; /*  */
  cursor: pointer; /*  */
}

.btn-blue {
  background-color: #66ccff; /*  */
  color: white; /*  */
}

.btn-gray {
  background-color: gray; /*  */
  color: white; /*  */
}
</style>

<style lang = "scss" scoped>
@import "../assets/css/album-content.scss";
</style>



