<template>
  <transition name="slide-fade">
    <div class="the-aside" v-if="showAside">
      <h2 class="title"></h2>

      <div class="menus">
        <ul v-if="listOfSongs.length">
          <li v-for="(item,index) in listOfSongs" :key="index" :class="{'is-play': id === item.id}"
              @click="toplay(item.id, item.url, item.pic, item.index, item.name, item.lyric)">
            <img
              :src="getUrl(item.pic)"
              alt=""
              :class="{ 'song-cover': true, 'is-play-song-cover': id === item.id }"
            />
            {{ replaceFName(item.name) }}
          </li>
        </ul>
        <p v-else class="no-music"></p>
      </div>

    </div>
  </transition>

</template>

<script>
import {mapGetters} from 'vuex'
import {getCollectOfUserId} from "../api";
import {mixin} from "../mixins";
import {url} from "nightwatch/examples/pages/home";

export default {
  name: 'the-aside',
  mixins: [mixin],
  computed: {
    ...mapGetters([
      'showAside', // 
      'listOfSongs', // 
      'id',          // id
      'loginIn',   // 
      'userId',    // id
      'isActive'    // 
    ])
  },

  mounted() {
    let _this = this
    document.addEventListener('click', function () {
      _this.$store.commit('setShowAside', false)
    }, true)

    if (!this.listOfSongs) {
      this.$store.commit("setListOfSongs", [])
    }

  },

  methods: {
    url() {
      return url
    },
    //  -- 
    replaceLName(str) {
      let arr = str.split('-')
      return arr[0]
    },
    // -- 
    replaceFName(str) {
      let arr = str.split('-')
      return arr[1]
    },

    getUrl(str) {
      return this.$store.state.configure.HOST + str
    },

    // 
    toplay: function (id, url, pic, index, name, lyric) {
      this.$store.commit('setId', id)
      this.$store.commit('setUrl', this.$store.state.configure.HOST + url)
      this.$store.commit('setPicUrl', this.$store.state.configure.HOST + pic)
      this.$store.commit('setListIndex', index)
      this.$store.commit('setTitle', this.replaceFName(name))
      this.$store.commit('setArtist', this.replaceLName(name))
      this.$store.commit('setLyric', this.parseLyric(lyric))

      if (this.loginIn) {
        getCollectOfUserId(this.userId)
          .then(res => {
            // 
            for (let item of res) {
              if (item.songId === id) {
                this.$store.commit('setIsActive', true);
                break;
              }
            }
          })
      }
    },
    // 
    parseLyric(text) {
      let lines = text.split('\n') // 
      let pattern = /\[\d{2}:\d{2}.(\d{3}|\d{2})]/g // 
      let result = []
      // 
      if (!(/\[.+]/.test(text))) {
        return [[0, text]]
      }
      // 
      while (lines.length !== 0 && !pattern.test(lines[0])) {
        lines = lines.slice(1)
      }
      // 
      for (let item of lines) {
        let time = item.match(pattern) // 
        let value = item.replace(pattern, '') // 
        for (let item1 of time) {
          let t = item1.slice(1, -1).split(':') // 
          if (value !== '') {
            result.push([parseInt(t[0], 10) * 60 + parseInt(t[1]), value])
          }
        }
      }
      //  --  -- 
      result.sort(function (a, b) {
        return a[0] - b[0]
      })
      return result
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/css/the-aside.scss";
</style>



