<template>
  <div class="play-bar" :class="{show:!toggle}">
    <div @click="toggle=!toggle" class="item-up" :class="{turn:!toggle}">
      <svg class="icon" :class="{ turn: !toggleView }">
        <use xlink:href="#icon-jiantou-xia-cuxiantiao"></use>
      </svg>
    </div>
    <div class="kongjian">
      <!--      -->
      <div class="item" @click="prev">
        <svg class="icon">
          <use xlink:href="#icon-ziyuanldpi"></use>
        </svg>
        <h1>{{ '#icon-ziyuanldpi' }}</h1>
      </div>
      <!-- -->
      <div class="item" @click="togglePlay">
        <svg class="icon">
          <use :xlink:href="playButtonUrl"></use>
        </svg>
      </div>
      <!--      -->
      <div class="item" @click="next">
        <svg class="icon">
          <use xlink:href="#icon-ziyuanldpi1"></use>
        </svg>
      </div>
      <!--    -->
      <div class="item-img" @click="toLyric">
        <img v-if="picUrl!==null" :src="picUrl"/>
      </div>
      <!--    -->
      <div class="playing-speed">
        <!--        -->
        <div class="current-time">{{ nowTime }}</div>
        <div class="progress-box">
          <div class="item-song-title">
            <div>{{ this.title }}</div>
            <div>{{ this.artist }}</div>
          </div>
          <div ref="progress" class="progress" @mousemove="mousemove">
            <!--            -->
            <div ref="bg" class="bg" @click="updatemove">
              <div ref="curProgress" class="cur-progress" :style="{width: curLength + '%'}"></div>
            </div>
            <!--            -->
            <div ref="idot" class="idot" :style="{left: curLength -1 + '%' }" @mousedown="mousedown"
                 @mouseup="mouseup"></div>
          </div>
        </div>
        <!--        -->
        <div class="left-time">{{ songTime }}</div>


        <div
          class="item"
          @click="changePlayState"
          @mouseenter="changeActive"
          @mouseleave="removeActive"
        >
          <div ref="info" class="info">
            <span>{{ info }}</span>
          </div>
          <img :src="control" class="icon">
        </div>


        <!--        -->
        <div class="item item-volume">
          <svg v-if="volume === 0" class="icon">
            <use xlink:href="#icon-yinliangjingyinheix"></use>
          </svg>
          <svg v-else class="icon">
            <use xlink:href="#icon-yinliang1"></use>
          </svg>
          <el-slider class="volume" v-model="volume" :vertical="true"></el-slider>
        </div>
        <!--        -->
        <div class="item" @click="collection">
          <svg :class="{active: isActive}" class="icon">
            <use xlink:href="#icon-xihuan-shi"></use>
          </svg>
        </div>
        <!--        -->
        <div class="item" @click="download">
          <svg class="icon">
            <use xlink:href="#icon-xiazai"></use>
          </svg>
        </div>
        <!--        -->
        <div class="item" @click="changeAside">
          <svg class="icon">
            <use xlink:href="#icon-liebiao"></use>
          </svg>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import {deleteCollectSong, download, getCollectOfUserId, setCollect, addRecentSong, addNums} from "../api/index";
import th from "element-ui/src/locale/lang/th";

// import {
//   getCollectBySongId,
// } from "../api/Collect";

export default {
  name: 'play-bar',
  data() {
    return {
      nowTime: '00:00', // 
      songTime: '00:00', // 
      curLength: 0, // 
      progressLength: 0, // 
      mouseStartX: 0, // 
      tag: false, // ,true
      volume: 50, // 50
      toggle: true, // 
      toggleView: true,
      liked: false,
      randomIndex: "",
      info: "",
    }
  },
  computed: {
    ...mapGetters([
      'id', // id
      'url', // 
      'isPlay', // 
      'playButtonUrl', // 
      'picUrl', // 
      'title', // 
      'artist', // 
      'duration', // 
      'curTime', // 
      'showAside', // 
      'listIndex', // 
      'listOfSongs', // 
      'autoNext', // 
      'loginIn',   // 
      'userId',    // id
      'isActive',   // 
      "control",
      "flag",
    ])
  },
  mounted() {
    this.progressLength = this.$refs.progress.getBoundingClientRect().width


    this.$store.commit("setPlayButtonUrl", "#icon-bofang");
    this.$store.commit("setControl", require("../assets/js/icon/icon-Loop.svg"));
    this.$store.commit("setFlag", 0);
    this.$store.commit("setRecentSongList", []);
    // if (this.picUrl === "" || this.picUrl === null) {
    //   this.$store.commit(
    //     "setPicUrl",
    //     "https://www.freemusic.ltd/avatar/1.jpeg"
    //   );
    // }


    document.querySelector('.item-volume').addEventListener('click', function (e) {
      document.querySelector('.volume').classList.add('show-volume')
      e.stopPropagation()
    }, false)
    document.querySelector('.volume').addEventListener('click', function (e) {
      e.stopPropagation()
    }, false)
    document.addEventListener('click', function () {
      document.querySelector('.volume').classList.remove('show-volume')
    }, false)
  },
  watch: {


    id() {
      this.getSongCollectState();
    },
    loginIn(val) {
      if (!val) {
        this.liked = false;
        this.$refs.like.children[0].style.color = "#ccc";
      } else {
        this.getSongCollectState();
      }
    },


    // 
    isPlay() {
      if (this.isPlay) {
        this.$store.commit('setPlayButtonUrl', '#icon-zanting')
      } else {
        this.$store.commit('setPlayButtonUrl', '#icon-bofang')
      }
    },
    curTime() {
      this.nowTime = this.formatSeconds(this.curTime)
      this.songTime = this.formatSeconds(this.duration)
      this.curLength = (this.curTime / this.duration) * 100
    },
    // 
    volume() {
      this.$store.commit('setVolume', this.volume / 100)
    },
    // 
    autoNext() {
      this.next()
    },
  },
  methods: {

    // 
    changePlayState() {
      if (this.url === "" || this.url === null) {
        this.$message.warning("");
      } else {
        if (this.control === require("../assets/js/icon/icon-SingleLoop.svg") && this.flag === 2) {
          this.$store.commit("setControl", require("../assets/js/icon/icon-Loop.svg"));
        }
        else if (this.control === require("../assets/js/icon/icon-Random.svg") && this.flag === 1) {
          this.$store.commit("setControl", require("../assets/js/icon/icon-SingleLoop.svg"));
        }
        else if (this.control === require("../assets/js/icon/icon-Loop.svg") && this.flag === 0) {
          this.$store.commit("setControl", require("../assets/js/icon/icon-Random.svg"));
        }
        if (this.control === require("../assets/js/icon/icon-Random.svg")) {
          this.$store.commit("setFlag", 1);
          this.info = "";
        }
        else if (this.control === require("../assets/js/icon/icon-SingleLoop.svg")) {
          this.$store.commit("setFlag", 2);
          this.info = "";
        }
        else if (this.control === require("../assets/js/icon/icon-Loop.svg")) {
          this.$store.commit("setFlag", 0);
          this.info = "";
        }
        this.$refs.info.classList.add("show-info");
      }
    },
    removeActive() {
      this.$refs.info.classList.remove("show-info");
    },
    changeActive() {
      this.$refs.info.classList.add("show-info");
    },
    // showVolume() {
    //   this.hide = !this.hide;
    // },


    // 
    togglePlay() {
      if (this.url !== "" && this.url !== null) {
        if (this.isPlay) {
          this.$store.commit("setIsPlay", false);
        } else {
          this.$store.commit("setIsPlay", true);
        }
      } else {
        this.$message.warning("");
      }
    },
    // 
    formatSeconds(value) {
      let theTime = parseInt(value)
      let result = ''
      let hour = Math.floor(theTime / 3600)
      let minute = Math.floor((theTime / 60) % 60)
      let second = Math.floor(theTime % 60)
      if (hour > 0) {
        if (hour < 10) {
          result = '0' + hour + ':'
        } else {
          result = hour + ':'
        }
      }
      if (minute > 0) {
        if (minute < 10) {
          result += '0' + minute + ':'
        } else {
          result += minute + ':'
        }
      } else {
        result += '00:'
      }
      if (second > 0) {
        if (second < 10) {
          result += '0' + second
        } else {
          result += second
        }
      } else {
        result += '00'
      }
      return result
    },
    // 
    mousedown(e) {
      this.mouseStartX = e.clientX
      this.tag = true
    },
    // 
    mouseup() {
      this.tag = false
    },
    // 
    mousemove(e) {
      if (this.url !== "" && this.url !== null) {
        // console.log(this.tag);
        if (!this.duration) {
          // console.log(":");
          return false
        }
        if (this.tag) {
          let movementX = e.clientX - this.mouseStartX // 
          let curLength = this.$refs.curProgress.getBoundingClientRect().width
          if (this.progressLength !== 0) {
            let newPercent =
              ((movementX + curLength) / this.progressLength) * 100;
            if (newPercent > 100) {
              newPercent = 100;
            }
            this.curLength = newPercent;
            this.mouseStartX = e.clientX;
            this.changeTime(newPercent);
          }
        }
      }
    },
    // 
    changeTime(percent) {
      let newCurTime = percent * 0.01 * this.duration
      this.$store.commit('setChangeTime', newCurTime)
    },
    // 
    updatemove(e) {
      this.$refs.bg.style.cursor = "pointer";
      if (this.url !== "" && this.url !== null) {
        if (!this.tag) {
          let curLength = this.$refs.bg.offsetLeft;
          if (this.progressLength !== 0) {
            let newPercent =
              ((e.clientX - curLength) / this.progressLength) * 100;
            if (newPercent > 100) {
              newPercent = 100;
            } else if (newPercent < 0) {
              newPercent = 0;
            }
            this.curLength = newPercent;
            this.changeTime(newPercent);
          }
        }
      } else {
        this.$message.warning("");
      }
    },

    // 
    changeAside() {
      // console.log("")
      this.$store.commit('setShowAside', true)
    },
    // 
    prev() {
      // 
      if (this.listIndex !== -1 && this.listOfSongs.length > 1) {
        if (this.flag !== 0) {
          if (this.flag === 1) {
            this.getRandomIndex();
            this.$store.commit("setListIndex", this.randomIndex);
          }
          if (this.flag === 2) {
            this.$store.commit("setListIndex", this.listIndex);
            this.$store.commit("setCurTime", 0);
            this.$store.commit("setChangeTime", 0);
          }
        } else {
          if (this.listIndex > 0) {
            this.$store.commit("setListIndex", this.listIndex - 1);
          } else {
            this.$store.commit("setListIndex", this.listOfSongs.length - 1);
          }
        }
        this.$store.commit("setCurTime", 0);
        this.$store.commit("setChangeTime", 0);
        if (this.isPlay) {
          this.toplay(this.listOfSongs[this.listIndex].url);
        } else {
          this.$store.commit("setIsPlay", true);
          this.toplay(this.listOfSongs[this.listIndex].url);
        }
        // this.addPlayCount(this.id);
        if (this.loginIn) {
          let RecentSong = {
            userId: this.userId,
            songId: this.id,
            count: 1,
          };
          addRecentSong(RecentSong);
        }
        addNums(this.id);
      } else {
        this.$store.commit("setIsPlay", false);
        this.$store.commit("setPlayButtonUrl", "#icon-bofang");
        this.$message.warning("");
      }
    },
    // 
    next() {
      // 
      if (this.listIndex !== -1 && this.listOfSongs.length > 1) {
        if (this.flag !== 0) {
          if (this.flag === 1) {
            this.getRandomIndex();
            this.$store.commit("setListIndex", this.randomIndex);
          }
          if (this.flag === 2) {
            this.$store.commit("setListIndex", this.listIndex);
            this.$store.commit("setCurTime", 0);
            this.$store.commit("setChangeTime", 0);
          }
        } else {
          if (this.listIndex < this.listOfSongs.length - 1) {
            this.$store.commit("setListIndex", this.listIndex + 1);
          } else {
            this.$store.commit("setListIndex", 0);
          }
        }
        this.$store.commit("setCurTime", 0);
        this.$store.commit("setChangeTime", 0);
        if (this.isPlay) {
          this.toplay(this.listOfSongs[this.listIndex].url);
        } else {
          this.$store.commit("setIsPlay", true);
          this.toplay(this.listOfSongs[this.listIndex].url);
        }
        // this.addPlayCount(this.id);
        if (this.loginIn) {
          let RecentSong = {
            userId: this.userId,
            songId: this.id,
            count: 1,
          };
          addRecentSong(RecentSong);
        }
        addNums(this.id);
      } else {
        this.$store.commit("setIsPlay", false);
        this.$store.commit("setPlayButtonUrl", "#icon-bofang");
        this.$message.warning("");
      }
    },
    getRandomIndex() {
      this.randomIndex = parseInt(Math.random() * this.listOfSongs.length, 10);
    },
    // 
    toplay: function (url) {
      if (url && url !== this.url) {
        this.$store.commit('setId', this.listOfSongs[this.listIndex].id);
        this.$store.commit('setUrl', this.$store.state.configure.HOST + url);
        this.$store.commit('setPicUrl', this.$store.state.configure.HOST + this.listOfSongs[this.listIndex].pic);
        this.$store.commit('setTitle', this.replaceFName(this.listOfSongs[this.listIndex].name));
        this.$store.commit('setArtist', this.replaceLName(this.listOfSongs[this.listIndex].name));
        this.$store.commit('setLyric', this.parseLyric(this.listOfSongs[this.listIndex].lyric));


        this.$store.commit(
          "setIntroduction",
          this.listOfSongs[this.listIndex].introduction
        );


        if (this.loginIn) {
          getCollectOfUserId(this.userId)
            .then(res => {
              // 
              for (let item of res) {
                if (item.songId === id) {
                  this.$store.commit('setIsActive', true);
                  break;
                }
              }
            })
        }
      }
    },
    //  -- 
    replaceLName(str) {
      let arr = str.split('-')
      return arr[0]
    },
    // -- 
    replaceFName(str) {
      let arr = str.split('-')
      return arr[1]
    },
    parseLyric(text) {
      let lines = text.split('\n') // 
      let pattern = /\[\d{2}:\d{2}.(\d{3}|\d{2})]/g // 
      let result = []
      // 
      if (!(/\[.+]/.test(text))) {
        return [[0, text]]
      }
      // 
      while (lines.length !== 0 && !pattern.test(lines[0])) {
        lines = lines.slice(1)
      }
      // 
      for (let item of lines) {
        let time = item.match(pattern) // 
        let value = item.replace(pattern, '') // 
        for (let item1 of time) {
          let t = item1.slice(1, -1).split(':') // 
          if (value !== '') {
            result.push([parseInt(t[0], 10) * 60 + parseInt(t[1]), value])
          }
        }
      }
      //  --  -- 
      result.sort(function (a, b) {
        return a[0] - b[0]
      })
      return result
    },
    // 
    toLyric() {
      if(this.picUrl === null){
        return;
      }
      this.$router.push({path: '/lyric'})
    },
    // 
    download() {
      console.log("download");
      if (this.url !== "" && this.url !== null) {
        if (this.loginIn) {
          download(this.url).then((res) => {
            let content = res.data;
            let eleLink = document.createElement("a");
            // 
            eleLink.download = `${this.artist}-${this.title}.mp3`;
            eleLink.style.dislpay = 'none';
            // blob
            let blob = new Blob([content]);
            eleLink.href = URL.createObjectURL(blob);
            // document
            document.body.appendChild(eleLink);
            // 
            eleLink.click();
            // 
            document.body.removeChild(eleLink);
            setTimeout(() => {
              this.$message.success("");
            }, 1000);
          });
        } else {
          this.$message.warning("");
        }
      } else {
        this.$message.warning("");
      }
    },


    getSongCollectState() {
      let CollectVo = {
        userId: this.userId,
        type: 0,
        songId: this.id,
        songListId: "",
      };
      // if (this.loginIn) {
      //   if (this.id !== "" && this.id !== null) {
      //     getCollectBySongId(CollectVo).then((res) => {
      //       if (res.data.code === 200) {
      //         this.liked = true;
      //         this.$refs.like.children[0].style.color = "#DC143C";
      //       } else {
      //         this.liked = false;
      //         this.$refs.like.children[0].style.color = "#ccc";
      //       }
      //     });
      //   }
      // }
    },


    // 
    collection() {
      if (this.loginIn) {
        var params = new URLSearchParams();
        params.append('userId', this.userId);
        params.append('type', 0);
        params.append('songId', this.id);
        setCollect(params)
          .then(res => {
            if (res.code === 1) {
              this.$store.commit('setIsActive', true);
              this.notify('', 'success');
            } else if (res.code === 2) {
              this.$store.commit('setIsActive', false);
              deleteCollectSong(this.userId, this.id);
              this.notify('', 'success');
            } else {
              this.notify('', 'error');
            }
          })
      } else {
        this.notify('', 'warning');
      }
    },


    // 
    notify(title, type) {
      this.$notify({
        title: title,
        type: type
      })
    }

  }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/play-bar.scss';
</style>



