<template>
  <div class = "my-songs">
    <el-button class = "create-btn" @click = "upload()"></el-button>
    <el-table :data="this.songs" style="width: 100%">
      <el-table-column prop="pic" label="" width="120">
        <template slot-scope="scope">
          <img :src="attachImageUrl(scope.row.pic)" alt="" style="width: 80px; height: 80px;">
        </template>
      </el-table-column>
      <el-table-column prop="name" label="" width="100px">
      </el-table-column>
      <el-table-column prop="style" label="" width="100px">
      </el-table-column>
      <el-table-column prop="introduction" label="" width="100px">
      </el-table-column>
    </el-table>

    <el-dialog
      :visible.sync = "addDialog"
      title = ""
      width = "50%"
      center
    >
      <el-form :model = "addForm" ref = "addForm" label-width = "80px" action = "" id = "tf">
        <div>
          <label></label>
          <el-input type = "text" name = "songName"></el-input>
        </div>
        <div>
          <label></label>
          <el-input type = "text" name = "introduction"></el-input>
        </div>
        <div>
          <label></label>
          <el-input type = "text" name = "style"></el-input>
        </div>
        <div>
          <label></label>
          <el-input type = "textarea" rows="8" name = "lyric"></el-input>
        </div>
        <div>
          <label></label>
          <br>
          <input type = "file" accept="audio/*" name = "file">
        </div>
      </el-form>
      <span slot = "footer">
        <el-button size = "mini" @click = "addDialog = false"></el-button>
        <el-button size = "mini" type = "primary" @click = "addSong"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import {songOfSingerId} from "../../api";
import {mixin} from "../../mixins";
export default {
  data() {
    return {
      addDialog: false,
      songs: [],
      addForm: {      // 
        songName: '',
        introduction: '',
        style: '',
        lyric: '',
      },
    }
  },
  mixins:[mixin],
  computed: {
    ...mapGetters([
      'loginIn',
      'userId',
      'username'
    ])
  },
  mounted() {
    this.songs = songOfSingerId(this.userId);
  },
  methods: {
    upload() {
      this.addDialog = true
    },
    addSong() {
      let form = new FormData(document.getElementById('tf'));
      form.append('singerId', this.userId);
      form.set('name', this.username + '-' + form.get('songName'));
      if (!form.get('lyric')) {
        form.set('lyric', '[00:00:00]');
      }
      let req = new XMLHttpRequest();
      req.onreadystatechange = function ()
      {
        /**
         req.readyState == 4 
         req.status == 200 
         **/
        if (req.readyState === 4 && req.status === 200) {
          let res = JSON.parse(req.response)
          if (res.code) {
            // this.getData()
            this.addForm = {};
            this.notify(res.msg, 'success')
          }
          else {
            this.notify('', 'error')
          }
        }
      };
      req.open('post', `${this.$store.state.HOST}/song/add`, false);
      req.send(form);
      this.addDialog = false;
    },
  }
}
</script>

<style scoped lang = "scss">
@import "../../assets/css/my-songs.scss";
</style>



