<template>
  <div>
    <div class="info">
      <div class="title">
        <span></span>
      </div>
      <hr />
      <div class="personal">
        <el-form :model="registerForm" ref="registerForm" label-width="70px" class="demo-ruleForm" :rules="rules">
          <el-form-item prop="username" label="">
            <el-input v-model="registerForm.username" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="name" label="">
            <el-input v-model="registerForm.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="password" label="">
            <el-input type="password" v-model="registerForm.password" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="sex" label="">
            <el-radio-group v-model="registerForm.sex">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
              <el-radio :label="3"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item prop="phoneNum" label="">
            <el-input v-model="registerForm.phoneNum" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="email" label="">
            <el-input v-model="registerForm.email" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="birth" label="">
            <el-date-picker type='date' :editable="false" v-model="registerForm.birth" placeholder="" style="width: 100%;"></el-date-picker>
          </el-form-item>
          <el-form-item prop="introduction" label="">
            <el-input v-model="registerForm.introduction" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="location" label="">
            <el-select v-model="registerForm.location" placeholder="" style="width: 100%;">
              <el-option v-for="item in cities" :key="item.index" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div class="btn">
          <div @click="saveMsg"></div>
          <div @click="goback(-1)"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import {rules,cities} from "../assets/data/form.js";
import {mixin} from '../mixins';
import {getUserOfId, updateUserMsg} from '../api/index'

export default {
  name: 'info',
  mixins: [mixin],
  data(){
    return  {
      registerForm: {
        username: '',     //
        name: '',         // 
        password: '',     // 
        sex: '',          // 
        phoneNum: '',     // 
        email: '',        // 
        birth: '',        // 
        introduction: '',    //
        location: ''      // 
      },
      usernameOrigin : '', // 
      nameOrigin : '',      // 
      cities: [],     //  -- 
      rules: {}   // 
    }
  },
  mounted() {
    this.getMsg(this.userId);
  },
  computed:{
    ...mapGetters([
      'userId'
    ])
  },
  created() {
    this.rules = rules;
    this.cities = cities;
  },
  methods: {
    getMsg(userId){
      getUserOfId(userId)
        .then(res => {
          this.registerForm.username = res.username;
          this.registerForm.name = res.name;
          this.registerForm.password = res.password;
          this.registerForm.sex = res.sex ;
          this.registerForm.phoneNum = res.phoneNum;
          this.registerForm.email = res.email;
          this.registerForm.birth = res.birth;
          this.registerForm.introduction = res.introduction;
          this.registerForm.location = res.location
          // 
          this.usernameOrigin = this.registerForm.username;
          // 
          this.nameOrigin = this.registerForm.name;
        })
        .catch(err => {
          console.log(err);
        })
    },
    saveMsg() {
      let _this = this;
      let d = new Date(this.registerForm.birth);
      let datetime
      if (d) {
        datetime = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
      }
      let params = new URLSearchParams();
      params.append('id' , this.userId);
      params.append('username', this.registerForm.username);
      params.append('usernameOrigin',this.usernameOrigin);
      params.append('name', this.registerForm.name);
      params.append('nameOrigin', this.nameOrigin);
      params.append('password', this.registerForm.password);
      params.append('sex', this.registerForm.sex);
      params.append('phoneNum', this.registerForm.phoneNum);
      params.append('email', this.registerForm.email);
      params.append('birth', datetime);
      params.append('introduction', this.registerForm.introduction);
      params.append('location', this.registerForm.location);
      // params.forEach((value, key)=>{
      //   console.log(key, value);
      // })
      updateUserMsg(params)
        .then(res => {
          if (res.code === 1) {
            // this.notify('dd')
            this.$store.commit('setUsername',this.registerForm.username);
            _this.notify('', 'success');
            setTimeout(function () {
              _this.changeIndex('');
              _this.$router.push({path : '/'});
            }, 1000);
          } else {
            _this.notify('', 'error');
          }
        })
        .catch(error => {
          _this.notify('', 'error');
        })
    },
    goback(index) {
      this.$router.go(index);
    },
    changeIndex(value) {
      this.$store.commit('setActiveName', value);
    }
  }
}

</script>

<style lang="scss" scoped>
@import '../assets/css/info.scss';
</style>




