<template>
  <div class="gradient-background">
  <!--  <loginLogo />-->
    <div class="signUp">
      <div class="signUp-head">
        <span></span>
      </div>
      <el-form :model="registerForm" ref="registerForm" label-width="80px" class="demo-ruleForm" :rules="rules">
        <el-form-item prop="username" label="">
          <el-input v-model="registerForm.username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="name" label="">
          <el-input v-model="registerForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="email" label="">
          <el-input v-model="registerForm.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="code" label="">
          <div class="code-input-group">
            <el-input v-model="code" placeholder=""></el-input>
            <el-button class="input-with-button" :loading="codeLoading" :disabled="isDisable" @click="sendMsg">
              {{ statusMsg }}
            </el-button>
          </div>
        </el-form-item>
        <el-form-item prop="password" label="">
          <el-input
            type="password"
            v-model="registerForm.password"
            placeholder=""
            :show-password="true"
          ></el-input>
          <i @click="togglePasswordVisibility"></i>
        </el-form-item>
        <el-form-item prop="duplicatePassword" label="">
          <el-input type="password" v-model="duplicatePassword" placeholder=""></el-input>
        </el-form-item>
        <div class="login-btn">
          <el-button @click="goback(-1)"></el-button>
          <el-button type="primary" @click="SignUp"></el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>


<script>
import loginLogo from '../components/LoginLogo.vue';
import {rules,cities} from "../assets/data/form.js";
import {mixin} from '../mixins';
import {SignUp, validate} from '../api/index'
export default {
  name : 'sign-up',
  mixins : [mixin],
  components: {
    loginLogo
  },
  data(){
    return  {
      statusMsg: '',      // 
      isDisable: false,
      codeLoading: false,
      verifyCode : '',    // 
      registerForm: {
        username: '',     //
        name: '',         // 
        password: '',     // 
        sex: '',          // 
        phoneNum: '',     // 
        email: '',        // 
        birth: '',        // 
        introduction: '',    //
        location: ''      // 
      },
      duplicatePassword : '', // 
      cities: [],     //  -- 
      rules: {},   // 
      code : '',    // 
      passwordVisible: false
    }
  },
  created() {
    this.statusMsg = '';
    this.rules = rules;
    this.cities = cities;
  },
  methods: {
    SignUp(){
      if(this.registerForm.password !== this.duplicatePassword){
        this.notify('','error');
        return;
      }
      if(this.verifyCode !== this.code){
        this.notify('','error');
        return;
      }
      let _this = this;
      let d = this.registerForm.birth
      let datetime
      if (d) {
        datetime = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
      }
      let params = new URLSearchParams();
      params.append('username',this.registerForm.username);
      params.append('name',this.registerForm.name);
      params.append('password',this.registerForm.password);
      this.registerForm.sex = 3; //  
      params.append('sex',this.registerForm.sex);
      params.append('phoneNum',this.registerForm.phoneNum);
      params.append('email',this.registerForm.email);
      params.append('birth', datetime);
      params.append('introduction',this.registerForm.introduction);
      params.append('location',this.registerForm.location);
      params.append('profilePicture','/img/Pic/default_avatar.jpg');
      SignUp(params)
        .then(res => {
          if(res.code === 1){
            _this.notify('', 'success');
            setTimeout(function (){
              _this.changeIndex('');
              _this.$router.push({path : `/login-in`});
            }, 2000);
          }
          else{
            _this.notify(`:${res.msg}`, 'error');
          }
        })
        .catch(err => {
          _this.notify('', 'error');
        })
    },
    sendMsg: function() {
      // 22373297@buaa.edu.cn
      const self = this
      let timerId;
      console.log(timerId)
      if (timerId) {
        return false;
      }
      this.codeLoading = true;
      let errorTimerId = window.setTimeout(() => {
        this.notify('', 'error');
        window.clearTimeout(errorTimerId);
        self.verifyCode = "******";
        self.codeLoading = false
        self.isDisable = false;
        self.statusMsg = '';
      }, 6000); // 6

      validate(this.registerForm.email).then(res => {
        this.notify(' 1 ', 'success');
        let count = 60
        this.verifyCode = res.verifyCode;
        self.codeLoading = false
        self.isDisable = true
        self.statusMsg = `${count--} `
        timerId = window.setInterval(function () {
          self.statusMsg = `${count--} `
          window.clearTimeout(errorTimerId);
          if (count <= -1) {
            console.log('clear' + timerId);
            window.clearInterval(timerId);
            self.verifyCode = "******";
            self.isDisable = false;
            self.statusMsg = '';
          }
        }, 1000)
      }).catch(err => {
        this.isDisable = false
        this.statusMsg = ''
        this.codeLoading = false
        window.clearTimeout(errorTimerId);
      })
    },
    goback(index){
      this.$router.go(index);
    },
    changeIndex(value){
      this.$store.commit('setActiveName' , value);
    },
    togglePasswordVisibility() {
      this.passwordVisible = !this.passwordVisible;
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/sign-up.scss';
</style>



